import './brief.css'
import { useState } from 'react'
import { Slider } from '@mui/material'
import {Box} from '@mui/material'
import {FormGroup} from '@mui/material'
import {FormControlLabel} from '@mui/material'
import {RadioGroup} from '@mui/material'
import {Radio} from '@mui/material'
import { useNavigate } from "react-router-dom";
const Brief  = () => {
    function ping() {
        fetch('https://telegram-middleware-send-message.onrender.com/').then(data => console.log(data));
    }
    ping()
    
    const navigate = useNavigate();

    const tg = window.Telegram.WebApp;

    tg.expand();

    tg.BackButton.show();
    tg.BackButton.onClick(() => {
        navigate('/');
    });

    const data = tg.initDataUnsafe;
   
    const [minPrice, setMinPrice] = useState(50000);
    const [maxPrice, setMaxPrice] = useState(5000000);
    const [minDays, setMinDays] = useState(3);
    const [maxDays, setMaxDays] = useState(365);
    const [name, setName] = useState(data.user.first_name);
    const [email, setEmail] = useState('');
    const [telegram, setTelegram] = useState('@' + data.user.username);
    const [taskOfGame, setTaskOfGame] = useState('');
    const [shortDescription, setShortDescription] = useState('');
    const [gameAudience, setGameAudience] = useState('');
    const [graphicsType, setGraphicsType] = useState('');
    const [gameStyle, setGameStyle] = useState('');
    const [isOnline, setIsOnline] = useState('');
    const [otherServices, setOtherServices] = useState('');
    const [hasDevelopments, setHasDevelopments] = useState('');
    const [priorities, setPriorities] = useState('');
    const [importantThings, setImportantThings] = useState('');
    const [otherThings, setOtherThings] = useState('');

    const [taskOfGameStyle, setTaskOfGameStyle] = useState('other__input hide');
    const [taskOfGameInput, setTaskOfGameInput] = useState('');
    const [gameStyleCSS, setGameStyleCSS] = useState('other__input hide');
    const [gameStyleInput, setGameStyleInput] = useState('');

    const formatPrice = (price) => {
        return new Intl.NumberFormat('ru-RU').format(price);
    }

    const handlePrice = ( event, newValue ) => {
      setMinPrice(newValue[0]);
      setMaxPrice(newValue[1]);
    };

    const formatDays = (days) => {
        if (days === 365) return '1 '
        if (days >= 31) {
            const months = Math.floor(days / 31);
            let postPrefix = '';
            if (months > 1 && months < 5) postPrefix = ' ';
            else if (months > 5) postPrefix = ' ';
            else postPrefix = ' ';
            return months + postPrefix;
        }
        let postPrefix = '';
        if (days === 21) postPrefix = ' ';
        else if (days < 25 & days > 21) postPrefix = ' ';
        else if (days < 5) postPrefix = ' ';
        else postPrefix = ' ';
        return days + postPrefix;
    }

    const handleTime = ( event, newValue ) => {
        setMinDays(newValue[0]);
        setMaxDays(newValue[1]);
      };

    const handleSubmit = ( event ) => {
        event.preventDefault();

        const data = {
            'minPrice': formatPrice(minPrice),
            'maxPrice': formatPrice(maxPrice),
            'minDays': minDays,
            'maxDays': maxDays,
            'name': name,
            'email': email,
            'telegram': telegram,
            'taskOfGame': taskOfGame !== '' ? taskOfGame : taskOfGameInput,
            'shortDescription': shortDescription,
            'gameAudience': gameAudience,
            'graphicsType': graphicsType,
            'gameStyle': gameStyle !== '' ? gameStyle : gameStyleInput,
            'isOnline': isOnline,
            'otherServices': otherServices,
            'hasDevelopments': hasDevelopments,
            'priorities': priorities,
            'importantThings': importantThings,
            'otherThings': otherThings,
        };

        fetch('https://telegram-middleware-send-message.onrender.com/send-message/', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        }).then(data => {
            navigate('/')
        });
    }

    return (
        <div className="brief ">
            <div className="container">
                <div className="brief__text-block">
                    <h2>
                        -
                    </h2>
                    <p>
                                 
                    </p>
                </div>

                <form action="#" onSubmit={handleSubmit} className = 'brief__form'>
                    <div className="brief__form-range">
                        <p> [#]  </p>
                        <Box >
                            <Slider
                                getAriaLabel={() => 'Price'}
                                value={[minPrice, maxPrice]}
                                onChange={handlePrice}
                                min={50000}
                                max={5000000}
                                step={10000}
                            />
                        </Box>

                        <div className="brief__form-range-positions">
                            <span className = 'first-pos'>{formatPrice(minPrice)} .</span>
                            <span className = 'sec-pos'>{formatPrice(maxPrice)} .</span>
                        </div>
                    </div>

                    <div className="brief__form-range" style = {{marginTop: 27}}>
                        <p> [#]  </p>
                        <Box >
                            <Slider
                                getAriaLabel={() => 'Price'}
                                value={[minDays, maxDays]}
                                onChange={handleTime}
                                min={3}
                                max={365}
                                step={1}
                            />
                        </Box>

                        <div className="brief__form-range-positions">
                            <span className = 'first-pos'>{formatDays(minDays)}</span>
                            <span className = 'sec-pos'>{formatDays(maxDays)}</span>
                        </div>
                    </div>


                    <div className="brief-naming">
                        <div className="brief__naming-block">
                            :
                            <input required placeholder=' ...' value={name} onChange={(event) => setName(event.target.value)}>

                            </input>
                        </div>
                        <div className="brief__naming-block">
                            TG:
                            <input required placeholder=' ...' value={telegram} onChange={(event) => setTelegram(event.target.value)}>

                            </input>
                        </div>
                        <div className="brief__naming-block">
                            Email:
                            <input placeholder=' ...' onChange={(event) => setEmail(event.target.value)}>

                            </input>
                        </div>
                    </div>
                   
                   <div className="brief__test">
                        <div className="brief__test-quest">
                            <p>    ?</p>
                        </div>
                        <FormGroup className='brief__test-answs'>
                            <RadioGroup
                                onChange={(event) => {
                                    console.log(event.target.value)
                                    if(event.target.value === '') {
                                        setTaskOfGameStyle('visiable other__input')
                                    } else {
                                        setTaskOfGameStyle('other__input hide')
                                    }
                                    setTaskOfGame(event.target.value)
                                }}
                                aria-labelledby="company-goal"
                                name="radio-buttons-group">
                                    
                                <FormControlLabel 
                                    value='  ' 
                                    control={<Radio required={true} />} 
                                    label="  "  
                                    className='brief__test-answ'/>
                                <FormControlLabel 
                                    value=' ' 
                                    control={<Radio required={true} />} 
                                    label=" "
                                    className='brief__test-answ' 
                                    aria-label='12'/>
                                <FormControlLabel 
                                    value=' , ,  '
                                    control={<Radio required={true} />}
                                    label=" , ,  "
                                    className='brief__test-answ'/>
                                <FormControlLabel 
                                    value=', '
                                    control={<Radio required={true} />}
                                    label=", "
                                    className='brief__test-answ'/>
                                <FormControlLabel 
                                    value=''
                                    control={<Radio required={true} />}
                                    label=" "
                                    className='brief__test-answ'/>
                            </RadioGroup>
                        </FormGroup>
                        <input placeholder='' className={taskOfGameStyle} onChange={(event) => {setTaskOfGameInput(event.target.value)}}></input>
                   </div>

                   <div className="brief__long-quest">
                        <p>
                            <b>    </b>  ,   , , ,       ,   
                        </p>

                        <textarea className='short-description' placeholder=' ...' onChange={(event) => {setShortDescription(event.target.value)}}>

                        </textarea>
                   </div>
                   <div className="brief__long-quest">
                        <p>
                         <b>   </b>   ,    .   -  (, , , ,  )
                        </p>

                        <textarea className='audition' placeholder=' ...' onChange={(event) => {setGameAudience(event.target.value)}}>

                        </textarea>
                   </div>

                   <div className="brief__test">
                        <div className="brief__test-quest">
                            <p><b>   </b>         </p>
                        
                        </div>
                        <FormGroup className='brief__test-answs'>
                            <RadioGroup onChange={(event) => {setGraphicsType(event.target.value)}}>
                                <FormControlLabel 
                                    value='2D'
                                    control={<Radio />} 
                                    label="2D"  
                                    className='brief__test-answ'/>
                                <FormControlLabel 
                                    value='3D'
                                    control={<Radio />} 
                                    label="3D"
                                    className='brief__test-answ' />
                                <FormControlLabel 
                                    value='2.5D'
                                    control={<Radio />} 
                                    label=" (2.5D)" 
                                    className='brief__test-answ'/>
                        
                            </RadioGroup>
                            
                        </FormGroup>  
                   </div>

                   <div className="brief__test">
                        <div className="brief__test-quest">
                            <p><b>   </b>        </p>
                        
                        </div>
                        <FormGroup className='brief__test-answs'>
                            <RadioGroup onChange={(event) => {
                                if(event.target.value === '') {
                                    setGameStyleCSS('visiable other__input')
                                } else {
                                    setGameStyleCSS('other__input hide')
                                }
                                setGameStyle(event.target.value)
                                }}>
                                <FormControlLabel
                                    value=' '
                                    control={<Radio />}
                                    label=" " 
                                    className='brief__test-answ'/>
                                <FormControlLabel 
                                    value='-'
                                    control={<Radio />} 
                                    label="-" 
                                    className='brief__test-answ' />
                                <FormControlLabel 
                                    value=''
                                    control={<Radio />} 
                                    label=""
                                    className='brief__test-answ'/>
                                <FormControlLabel 
                                    value=''
                                    control={<Radio />}
                                    label=""
                                    className='brief__test-answ'/>
                            </RadioGroup>
                        </FormGroup>  
                        <input placeholder='' className={gameStyleCSS} onChange={(event) => {setGameStyleInput(event.target.value)}}></input>
                   </div>

                   <div className="brief__test">
                        <div className="brief__test-quest">
                            <p>
                            <b>  - </b>          ,      
                            </p>
                        
                        </div>
                        <FormGroup className='brief__test-answs'>
                            <RadioGroup onChange={(event) => {setIsOnline(event.target.value)}}>
                                <FormControlLabel
                                    value=' '
                                    control={<Radio />}
                                    label="  (, ,  , , -)" 
                                    className='brief__test-answ'/>
                                <FormControlLabel
                                    value=' '
                                    control={<Radio />}
                                    label="   " 
                                    className='brief__test-answ'/>
                                
                            </RadioGroup>
                        </FormGroup>  
                   </div>
                   <div className="brief__test">
                        <div className="brief__test-quest">
                            <p>
                            <b>      </b>  , , , ,  ,  
                            </p>
                        </div>
                        <FormGroup className='brief__test-answs'>
                            <RadioGroup onChange={(event) => {setOtherServices(event.target.value)}}>
                                <FormControlLabel
                                    value=' '
                                    control={<Radio />}
                                    label="" 
                                    className='brief__test-answ'/>
                                <FormControlLabel
                                    value='C  '
                                    control={<Radio />}
                                    label="" 
                                    className='brief__test-answ'/>
                                
                            </RadioGroup>
                        </FormGroup>  
                   </div>
                   <div className="brief__test">
                        <div className="brief__test-quest">
                            <p>
                            <b>    </b>  , , . ,  ,   .
                            </p>
                        </div>
                        <FormGroup className='brief__test-answs'>
                            <RadioGroup onChange={(event) => {setHasDevelopments(event.target.value)}}>
                                <FormControlLabel
                                    value=' '
                                    control={<Radio />}
                                    label="" 
                                    className='brief__test-answ'/>
                                <FormControlLabel
                                    value=' '
                                    control={<Radio />}
                                    label="" 
                                    className='brief__test-answ'/>
                            </RadioGroup>
                        </FormGroup>  
                   </div>

                   <div className="brief__long-quest">
                        <p>
                         <b>   </b> 
                        </p>

                        <textarea className = 'priority' placeholder=' ...' onChange={(event) => {setPriorities(event.target.value)}}>

                        </textarea>
                   </div>
                   <div className="brief__long-quest">
                        <p>
                         <b>        </b>  , ,      ,       . 
                        </p>

                        <textarea className = 'priority' placeholder=' ...' onChange={(event) => {setImportantThings(event.target.value)}}>

                        </textarea>
                   </div>
                   <div className="brief__long-quest">
                        <p>
                         - ,      
                        </p>

                        <textarea className = 'priority' placeholder=' ...' onChange={(event) => {setOtherThings(event.target.value)}}>

                        </textarea>
                   </div>

                   <button type ='submit' onClick={handleSubmit} className='submit'></button>
                </form>
            </div>
        </div>
        
    )
}

export default Brief